.card{border-radius:1rem;width:100%;box-shadow:0 4px 6px rgba(0,0,0,.1);display:flex;flex-direction:row;align-items:center;gap:1.5rem;justify-content:center}.header{display:flex;align-items:center;gap:.75rem;margin-bottom:0}.logo{width:2rem;height:2rem;object-fit:contain}.title{font-size:1.25rem;font-weight:bold;white-space:nowrap}.selectContainer{display:flex;align-items:center}.selectBox{padding:.4rem .6rem;font-size:.9rem;border-radius:.5rem}.details{display:flex;flex-direction:row;color:#374151;font-size:.875rem;gap:10px}.error{color:red}.dark{background-color:var(--dark);color:var(--surface)}.light{background-color:var(--white);color:var(--on-surface)}.volumeChange{padding:2px 8px;border-radius:24px;text-align:center}.volumeChange.negative{background-color:var(--critical);color:var(--white)}.volumeChange.positive{background-color:var(--success);color:var(--white)}